@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
}

body {
  margin: 0;
  background-color: #f0eeef;
}

ul {
  padding: 0;
  margin: 0;
}

textarea {
  resize: none;
}

textarea,
select,
button {
  outline: none;
  font-family: Roboto, sans-serif;
}

.app {
  display: flex;
  justify-content: center;
  grid-gap: 60px;
  gap: 60px;
  margin: 0 auto;
  padding: 30px;
}

@media (max-width: 700px) {
  .app {
    flex-direction: column;
  }
}

.app__title {
  text-align: center;
}

.form {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
}

.form__select,
.form__textarea,
.form__button {
  padding: 10px;
  font-size: 20px;
  border-radius: 5px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: -6px -6px 16px rgba(255, 255, 255, 0.5),
    6px 6px 16px rgba(209, 205, 199, 0.5);
}

.form__textarea {
  height: 100px;
}

.form__button {
  border: none;
  background-color: #00a82f;
  color: #fff;
  transition: background-color 0.3s;
}

.form__button:hover {
  background-color: #009129;
}

.form__error {
  color: red;
  margin-bottom: -10px;
  font-size: 14px;
}

.todo {
  display: grid;
  grid-template-columns: 2fr 4fr 2fr;
  grid-gap: 60px;
  gap: 60px;
  padding: 20px 40px;
  background-color: #fff;
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  box-shadow: -6px -6px 16px rgba(255, 255, 255, 0.5),
    6px 6px 16px rgba(209, 205, 199, 0.5);
  transition: transform 0.3s, box-shadow 0.3s, border-radius 0.3s;
}

.todo:hover {
  box-shadow: -16px -16px 36px 10px rgba(255, 255, 255, 0.3),
    16px 16px 36px 10px rgba(209, 205, 199, 0.3);
  border-radius: 15px;
  transform: scale(1.02);
}

@media (max-width: 1000px) {
  .todo {
    grid-template-columns: 1fr;
    grid-gap: 20px;
    gap: 20px;
  }
}

.todo__title {
  margin-bottom: 15px;
  line-height: 19px;
}

@media (max-width: 1000px) {
  .todo__title {
    margin-bottom: 5px;
  }
}

.todo__info {
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  position: relative;
}

.todo__icon {
  background: url(../../static/media/completed.2025d9fb.svg) no-repeat center;
  width: 20px;
  height: 20px;
  position: absolute;
  left: -35px;
}

.todo__icon--not-completed {
  background-image: url(../../static/media/not_completed.749645a0.svg);
}

.todo__list {
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  max-width: 800px;
}

